* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: hidden;
}
body{
    background-color: rgba(0, 0, 0, 0.801)
}
header{
    width: 100%;
    height: 3.5rem;
    background-color: rgb(54, 1, 1);
    margin-bottom: 20px;
    padding: 10px;
}
.inputForm{
    float: right;
  
}
.inputForm form input{
    background-color: rgb(203, 206, 207);
    padding: 5px;
    outline: none;
    border: none; 
    color: red;
    font-weight: bold; 
    border: 2px solid #dadada;
    border-radius: 7px;
}
.inputForm form input:focus { 
    outline: none;
    border-color: #9ecaed;
    box-shadow: 0 0 10px #9ecaed;
}
.movie-container{
 display: flex;
 flex-wrap: wrap;
 width: 100%;
 margin: auto;
 justify-content: center;
}
.movie-box{
    margin: 15px;
    width: 350px;
    box-shadow: 0 2px 2px rgba(54, 1, 1, 0.329);
    border-radius: 4px 4px 0 0;
    background-color: rgb(105, 105, 105);
    overflow: hidden;
    object-fit: cover;
}
.movie-zone{
    margin-left: 15px;
    color:  blue;
    text-shadow: 0 4px 4px black;
}
.inner-movie-box img{
    width: 100%;
    height: 100%;
    transition: transform 0.4s ease-in-out;
  }
  .inner-movie-box{
      overflow: hidden;
      height: 400px;
      object-fit: cover;
  }
  .movie-box img:hover{
      transform: scale(1.2);
  }
.movie-box-bottom{
   display: flex;
   justify-content: space-between;
   padding: 10px 5px;
   z-index: 10;
}
.rating.green{
    color: rgb(6, 231, 6);
}
.rating.orange{ color: orange;}
.rating.red{ color: red;}
/*------------------------*/
/*---single movie details*/
.single-movie-detail-container
{
    margin: 20px;
    padding: 20px;
    
}
@media screen and (min-width:800px) {
    .single-movie-detail-container{
        margin: 20px auto;
        width: 80%;
    }
}

.movie-detail-inner-container{
    width: auto;
    height: auto;
    display: flex;
    padding: 5px;
    background-color: rgba(54, 1, 1, 0.356) ;
}

.detail-image{
   height: 100%; 
   max-width: 50%;
   object-fit: cover;
   margin-right: 20px;
   overflow-y: hidden;


}
.detail-image img{
    width: 100%;
    height: 100%;  
}
.details{
    width: 50%;
    color: white;
    display: flex;
    flex-direction: column;
   
}
.details p{
    float: left;
    margin-bottom: 10px;
}
.details span{
    color: blue;
}
@media screen and (max-width:600px) {
    .movie-detail-inner-container{
        flex-direction: column;
    }
    .detail-image{
        width: 100%;
        height: auto;
        margin: auto;
    }
    .detail-image img{
        
        height: auto;  
    }
    .details{
        width: 100%;
        padding: 15px;
    }
}
.go-back-button{
    
    padding:5px ;
    font-size: 1.2rem;
    background-color: gray;
    outline: none;
    border: none;
}
.go-back-button:hover{
    background-color: black;
    color: white;
}